import React, {useState} from 'react';
import style from './ProjectPCView.module.css';

import ProjectData from '../../dataset/project.json';

import DivComponent from './DivComponent.js';

function ProjectPCView(){
    const [webScroll, setWebScroll] = useState(0);      //    
    const [dataScroll, setDataScroll] = useState(0);    //    
    const [aiScroll, setAiScroll] = useState(0);        //    


    //   (from json) -  , , 
    const webProject = ProjectData.Web.map((item, idx) => {
        return(
            <DivComponent
                title={item.title}
                image={item.image}
                text={item.text}>
            </DivComponent>
        );
    });
    const dataProject = ProjectData.Data.map((item, idx) => {
        return(
            <div className={style.per} style={{animationDelay:'0.3s'}}>
                {item.title}
            </div>
        );
    })
    const aiProject = ProjectData.AI.map((item, idx) => {
        return(
            <div className={style.per} style={{animationDelay:'0.3s'}}>
                {item.title}
            </div>
        );
    })

    //    -  , , , 
    const webScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div   
        const height = e.target.scrollHeight - e.target.clientHeight;     // div   ( div height (300))
        const percentage = (top / height) * e.target.clientWidth;      //  
        setWebScroll(percentage);
    }
    const dataScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div   
        const height = e.target.scrollHeight - e.target.clientHeight;     // div   ( div height (300))
        const percentage = (top / height) * e.target.clientWidth;      //  
        setDataScroll(percentage);
    }
    const aiScrollEvent = (e) => {
        const top = e.target.scrollTop;     // div   
        const height = e.target.scrollHeight - e.target.clientHeight;     // div   ( div height (300))
        const percentage = (top / height) * e.target.clientWidth;      //  
        setAiScroll(percentage);
    }


    return(
        <div className={style.wrapper}>
            <div className={style.menu}>WEB</div>
            <div className={style.menu}>DATA</div>
            <div className={style.menu}>AI</div>

            <div className={style.scroll}> <span style={{width:webScroll}}></span> </div>
            <div className={style.scroll}> <span style={{width:dataScroll}}></span> </div>
            <div className={style.scroll}> <span style={{width:aiScroll}}></span> </div>

            {/*   */}
            <div className={style.content} onScroll={webScrollEvent}>
                {webProject}
            </div>

            {/*   */}
            <div className={style.content} onScroll={dataScrollEvent}>
                {dataProject}
            </div>

            {/*   */}
            <div className={style.content} onScroll={aiScrollEvent}>
                {aiProject}
            </div>
        </div>
    );
}

export default ProjectPCView;