import React, {useState, useEffect} from 'react';
import style from './Contact.module.css';

import link from './link_icon.png';
import email from './mail_icon.png';
import phone from './phone_icon.png';

function Contact(){
    const [contentDisplay, setContentDisplay] = useState('none');   //   (none: x / block: )

    //    -    
    useEffect(() => {
        const timer = setTimeout(() => {
            setContentDisplay('grid');
        }, 4000);
        return () => clearTimeout(timer);
    }, []);

    function goBlog(){
        window.open('https://movancing.tistory.com/');
    }
    function goGitStorage(){
        window.open('https://github.com/ksower');
    }

    return(
        <div>
            {/*    */}
            <div className={style.page_title}>
                <svg height="100" stroke="#000000" strokeWidth="3" className={style.text_line} width="100%">
                    <text x="50%" y="50%" dominantBaseline="middle" textAnchor="middle" >
                        Contact
                    </text>
                </svg>
            </div>

            <div className={style.wrapper} style={{display:contentDisplay}}>
                <div className={style.form}>
                    <div id={style['info']}>
                        <div>
                            <img src={email}/>
                        </div>
                        <p>namkey11@naver.com</p>
                    </div>
                    <div id={style['info']}>
                        <div>
                            <img src={phone}/>
                        </div>
                        <p id={style['link']} style={{cursor:'pointer'}} onClick={goBlog}>https://movancing.tistory.com</p>
                        </div>
                    <div id={style['info']}>
                        <div>
                            <img src={link}/>
                        </div>
                        <p id={style['link']} style={{cursor:'pointer'}} onClick={goGitStorage}>https://github.com/ksower</p>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Contact;