import React, { useState, useEffect } from 'react';
import style from './Profile.module.css';

import Face from './face.jpg';

function Profile(){
    const [contentDisplay, setContentDisplay] = useState('none');   //   (none: x / block: )

    //    -    
    useEffect(() => {
        const timer = setTimeout(() => {
            setContentDisplay('grid');
        }, 4000);
        return () => clearTimeout(timer);
    }, []);


    return(
        <div>
            {/*    */}
            <div className={style.page_title}>
                <svg height="100" stroke="#000000" strokeWidth="3" className={style.text_line} width="100%">
                    <text x="50%" y="50%" dominantBaseline="middle" textAnchor="middle" >
                        Profile
                    </text>
                </svg>
            </div>

            <div className={style.wrapper} style={{display:contentDisplay}}>
                <div className={style.me}>
                    <img id={style['photo']} src={Face}/>

                    <div id={style['intro']}>
                        <p><b></b> / Data Engineer</p>
                        <div>"  ,   AI      ."</div>
                    </div>
                </div>

                <div className={style.exp}>
                    <div>
                        <div> </div>
                                ,      .
                    </div>

                    <div>
                        <div>  </div>
                        1           , YOLO LSTM        .
                    </div>

                    <div>
                        <div> </div>
                              ,       .
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Profile;